import React from 'react';
import { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate } from "react-router-dom";
import './App.css';
import { Upperbar } from './upperbar';
import { Content } from './content';
import { Contact } from './contact';

const App: React.FC = () => {
  const [lan, setLan] = useState<string>("cz");
  return (
    <Router>
      <div className="App">
        <Upperbar lan={lan} setLan={setLan}/>
        {/*<div className='galaxy'></div>*/}
        {/*showcontact === 1 ?
          <ContactButton setter={setShowcontact}/>
          :
          <></>
        */}
        <Routes>
          <Route path="/" element={<Content lan={lan} />} />
          <Route path="/contact" element={<Contact lan={lan} />} />
        </Routes>
      </div>
    </Router>
  );
};

const ContactButton: React.FC = () => {
  const navigate = useNavigate();
  function contact(): void {
    navigate("/contact");
  }

  return <div className='contact' onClick={contact}></div>;
};

export {App, ContactButton};
