//import {useState, useRef } from 'react';
import React, { useState } from 'react';
import {animated, useSpring } from '@react-spring/web'
import './contact.css'

interface launguage2{
    lan: string,
}

interface Translations {
    cz: {
        [key: string]: string;
    };
    eng: {
        [key: string]: string;
    };
}

const Contact: React.FC<launguage2> = ({lan}) => {
    /*const [ref, inView] = useInView();
    const [ref2, inView2] = useInView();
    const [ref3, inView3] = useInView();
    const [ref4, inView4] = useInView();*/
    //const targetRef = useRef(null);
    const [e_error, setEError] = useState(false);
    const [m_error, setMError] = useState(false);
    const [sucmsg, setSucmsg] = useState("suc");
    const [springs, api] = useSpring(() => ({
        delay:1000,
        from: { x: '-100vw', rotate:'0deg' },
        to: {x:"0vw", rotate:'0deg'},
        reset: true,
        /*config: {
            tension: 150,
            friction: 150,
            mass: 50
        },*/
    }))
    const [springs2, api2] = useSpring(() => ({
        from: { x: '-100vw', rotate:'0deg' },
        to: {x:"-100vw", rotate:'0deg'},
        reset: true,
        /*config: {
            tension: 150,
            friction: 150,
            mass: 50
        },*/
    }))
    function translate(msg: string, lng: string) {
        const translations: Translations = require('./translations').default;
        return translations[lng as keyof Translations][msg];
    }
    async function submit(){
        let emailInput = document.getElementById('emailinput') as HTMLInputElement | null;
        let email = emailInput?.value;
        //
        let messageInput = document.getElementById('messageinput') as HTMLTextAreaElement | null;
        let message = messageInput?.value;
        //
        let has = email?.search('@');
        changeemail()
        changemessage()
        if(e_error||m_error||message===""||email===""||has === -1){
            //SOMETHING IS NOT CORRECT
        }else{
            //SEND EMAIL
            try {
                let response = await fetch('http://localhost:3001/send-email', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({ email, message }),
                });
        
                if (response.ok) {
                    setSucmsg("suc")
                    let m = document.getElementById('success')
                    if(m){
                        m.style.color = "rgb(0, 194, 0)"
                    }
                    api.start({
                        from: { x: '0vw', rotate:'0deg' },
                        to: {x:"100vw", rotate:'0deg'},
                        /*config: {
                            tension: 150,
                            friction: 150,
                            mass: 50
                        },*/
                    })
                    api2.start({
                        delay:450,
                        reset:true,
                        from: { x: '-100vw', rotate:'0deg' },
                        to: {x:"0vw", rotate:'0deg'},
                        /*config: {
                            tension: 150,
                            friction: 150,
                            mass: 50
                        },*/
                    })
                    setTimeout(function () { 
                        api.start({
                            reset:true,
                            from: { x: '-100vw', rotate:'0deg' },
                            to: {x:"0vw", rotate:'0deg'},
                            /*config: {
                                tension: 150,
                                friction: 150,
                                mass: 50
                            },*/
                        })
                        api2.start({
                            from: { x: '0vw', rotate:'0deg' },
                            to: {x:"100vw", rotate:'0deg'},
                            /*config: {
                                tension: 150,
                                friction: 150,
                                mass: 50
                            },*/
                        })
                    }, 2000)
                } else {
                    setSucmsg("err")
                    api.start({
                        from: { x: '0vw', rotate:'0deg' },
                        to: {x:"100vw", rotate:'0deg'},
                        /*config: {
                            tension: 150,
                            friction: 150,
                            mass: 50
                        },*/
                    })
                    api2.start({
                        delay:450,
                        from: { x: '-100vw', rotate:'0deg' },
                        to: {x:"0vw", rotate:'0deg'},
                        /*config: {
                            tension: 150,
                            friction: 150,
                            mass: 50
                        },*/
                    })
                }
            } catch (error) {
                setSucmsg("err")
                let m = document.getElementById('success')
                if(m){
                    m.style.color = "red"
                }
                    api.start({
                        from: { x: '0vw', rotate:'0deg' },
                        to: {x:"100vw", rotate:'0deg'},
                        /*config: {
                            tension: 150,
                            friction: 150,
                            mass: 50
                        },*/
                    })
                    api2.start({
                        delay:450,
                        reset:true,
                        from: { x: '-100vw', rotate:'0deg' },
                        to: {x:"0vw", rotate:'0deg'},
                        /*config: {
                            tension: 150,
                            friction: 150,
                            mass: 50
                        },*/
                    })
                    setTimeout(function () { 
                        api.start({
                            reset:true,
                            from: { x: '-100vw', rotate:'0deg' },
                            to: {x:"0vw", rotate:'0deg'},
                            /*config: {
                                tension: 150,
                                friction: 150,
                                mass: 50
                            },*/
                        })
                        api2.start({
                            from: { x: '0vw', rotate:'0deg' },
                            to: {x:"100vw", rotate:'0deg'},
                            /*config: {
                                tension: 150,
                                friction: 150,
                                mass: 50
                            },*/
                        })
                    }, 2000)
            }
        }
        //
    }
    function changeemail(){
        let emailInput = document.getElementById('emailinput') as HTMLInputElement | null;
        let email = emailInput?.value;
        if(email === ""){
            setEError(true);
        }else{
            setEError(false);
        }
    }
    function changemessage(){
        let messageInput = document.getElementById('messageinput') as HTMLTextAreaElement | null;
        let message = messageInput?.value;
        if(message === ""){
            setMError(true);
        }else{
            setMError(false);
        }
    }
    return (
        <div className='contactscreen'>
           <animated.div className='form' style={{...springs}}>
                <div className='email'>
                    <div className='form-header'>
                        <span>{translate('email',lan)}</span>
                    </div>
                    <input className="input" onChange={changeemail} id='emailinput' type="email" required placeholder={translate('enteremail',lan)}></input>
                    {
                        e_error ?
                            <span className='wrong'>{translate('wrongemail',lan)}</span>
                        :
                        <></>
                    }
                </div>
                <div className='message'>
                    <div className='form-header' id="messageheader">
                        <span>{translate('message',lan)}</span>
                    </div>
                    <textarea className="input" onChange={changemessage} id='messageinput' required placeholder={translate('entermessage',lan)}></textarea>
                    {
                        m_error ?
                            <span className='wrong'>{translate('wrongmessage',lan)}</span>
                        :
                        <></>
                    }
                </div>
                <div className='button'>
                    <button id='submit' onClick={submit}>{translate('submit',lan)}</button>
                </div>
           </animated.div>
           <animated.span id="success" style={{...springs2}}>{translate(sucmsg,lan)}</animated.span>
        </div>
      );
}
export {Contact};