import {useState } from 'react';
import React from 'react';
import './Upper.css';
import { useSpring, animated, useScroll } from '@react-spring/web'
import { useNavigate } from 'react-router-dom';
//import {Content} from './content';

//import translations from './translations';

interface launguage{
    lan: string,
    setLan: React.Dispatch<React.SetStateAction<string>>,
}

interface Translations {
    cz: {
        [key: string]: string;
    };
    eng: {
        [key: string]: string;
    };
}

const Upperbar: React.FC<launguage> = ({ lan, setLan}) => {
    const [color,setColor] = useState("linear-gradient(0deg, rgba(0,0,0,0.9206057422969187) 0%, rgba(0,0,0,0.9822303921568627) 50%, rgba(3,0,41,1) 100%)");
    const navigate = useNavigate();
    const [springs] = useSpring(() => ({
        delay:100,
        from: { y: -1000 },
        to: {y:0},
        reset: true,
    }))
    const [sp, api2] = useSpring(() => ({
        from: { y: -1000 },
        to: {y:0},
    }))
    function translate(msg: string, lng: string) {
        const translations: Translations = require('./translations').default;
        return translations[lng as keyof Translations][msg];
    }
    function changelaunguage(){
        if(lan === "cz"){
            setColor("linear-gradient(0deg, rgba(0,0,0,0.9206057422969187) 0%, rgba(0,0,0,0.9822303921568627) 50%, rgba(41,0,0,1) 100%)")
            setLan("eng");
        }else{
            setColor("linear-gradient(0deg, rgba(0,0,0,0.9206057422969187) 0%, rgba(0,0,0,0.9822303921568627) 50%, rgba(3,0,41,1) 100%)")
            setLan("cz");
        }
        api2.start({
            reset:true,
            from: {
              y: -1000,
            },
            to: {
              y: 0,
            },
          })
    }
    function scrollToRef(rf:number) {
        // Write logic to scroll to the desired refIndex
        home();
        setTimeout(function () { 
            switch (rf) {
                case 1:
                    document.getElementById("about")?.scrollIntoView({ behavior: 'smooth', block: 'center' });
                    break;
                case 2:
                    document.getElementById("projects")?.scrollIntoView({ behavior: 'smooth', block: 'center' });
                    break;
                case 3:
                    document.getElementById("education")?.scrollIntoView({ behavior: 'smooth', block: 'center' });
                    break;
                case 4:
                    document.getElementById("experience")?.scrollIntoView({ behavior: 'smooth', block: 'center' });
                    break;
                default:
                    document.getElementById("about")?.scrollIntoView({ behavior: 'smooth', block: 'center' });
            }
        }, 250)
    }
    
    function home(){
        navigate('/');
        document.getElementById("land")?.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
    return (
      <animated.div className="upperbar" style={
        {
            /*background:color,*/
            ...springs
        }}>
        <div className='leftpart'>
            <span id='name' onClick={home}>{translate("name",lan)}</span>
        </div>
        <div className='rightpart'>
            <animated.a style={{...sp}} onClick={() => scrollToRef(1)}>{translate("about",lan)}</animated.a>
            <animated.a style={{...sp}} onClick={() => scrollToRef(2)}>{translate("projects",lan)}</animated.a>
            <animated.a style={{...sp}} onClick={() => scrollToRef(3)}>{translate("education",lan)}</animated.a>
            <animated.a style={{...sp}} onClick={() => scrollToRef(4)}>{translate("experience",lan)}</animated.a>
            <animated.a id="launguage" style={{...sp}} onClick={changelaunguage}>{translate("lang",lan)}</animated.a>
            {/*<div id="launguage" onClick={changelaunguage}>{translate("lang",lan)}</div>*/}
            
        </div>
      </animated.div>
    );
  };
  
  export {Upperbar};