//import {useState, useRef } from 'react';
import React, { useState } from 'react';
import { useInView, animated, useSpring, config, easings, useScroll  } from '@react-spring/web'
import './content.css'
import {ContactButton} from './App';
import Collapsible from 'react-collapsible';

interface launguage2{
    lan: string,
}

interface Translations {
    cz: {
        [key: string]: string;
    };
    eng: {
        [key: string]: string;
    };
}

const Content: React.FC<launguage2> = ({lan}) => {
    const [ref, inView] = useInView();
    const [ref2, inView2] = useInView();
    const [ref3, inView3] = useInView();
    const [ref4, inView4] = useInView();
    //const targetRef = useRef(null);
    const [text,setText] = useState("Discord");
    const [spr, ap] = useSpring(() => ({
        delay:1000,
        from: { y: '-300vw' },
        to: {y:'0vw'},
    }))
    function translate(msg: string, lng: string) {
        const translations: Translations = require('./translations').default;
        return translations[lng as keyof Translations][msg];
    }
    function redirect(type:string){
        if(type==="none"){
            window.location.replace("https://github.com/Jimmy-88");
        }
    }
    return (
        <div className='contentbox'>
            <animated.div ref={ref} style={{/*marginRight:inView? "0vw": "110vw",*/ ...spr}} id="land" className='landing'>
                <span className='l1'>
                    {translate("welcome",lan)}
                </span>
            </animated.div>
            <animated.div ref={ref} style={{marginRight:inView? "0vw": "110vw", transition: "0.5s ease-in-out"}} id="about" className='content'>
                <span className='header'>
                    {translate("aboutheader",lan)}
                </span>
                <span className='text'>
                    {translate("abouttext",lan)}
                    <div className='buttons1'>
                        <span className='button1' id='discord' onMouseEnter={()=>{setText("jimmy8.")}} onMouseLeave={()=>{setText("Discord")}}>
                            {text}
                        </span>
                        {/*<span className='button1' id='cv'>
                            {"CV"}
                        </span>*/}
                        <span className='button1' id='github' onClick={()=> redirect("none")}>
                            {"GitHub"}
                        </span>
                    </div>
                </span>
            </animated.div>
            <animated.div ref={ref2} style={{marginRight:inView2? "0vw": "110vw", transition: "0.5s ease-in-out"}} id="projects" className='content'>
                <span className='header'>{translate("projectsheader",lan)}</span>
                <span className='text'>{translate("projectstext",lan)}</span>
            </animated.div>
            <animated.div ref={ref3} style={{marginRight:inView3? "0vw": "110vw", transition: "0.5s ease-in-out"}} id="education" className='content'>
                <span className='header'>{translate("educationheader",lan)}</span>
                <span className='text'>
                    <ul className='programinglaunguages'>  
                        <li>
                            Gymnzium Matye Lercha - Brno (2020-2024)
                        </li>
                        <li>
                            {"Provozn ekonomick fakulta Mendeovy Univerzity - Brno (2024->)"}
                        </li>
                    </ul>
                </span>
            </animated.div>
            <animated.div ref={ref4} style={{marginRight:inView4? "0vw": "110vw", transition: "0.5s ease-in-out"}} id="experience" className='content'>
                <span className='header'>{translate("experienceheader",lan)}</span>
                <span className='text'>
                    {translate("experiencetext",lan)}
                    <div className='expand'>
                        <div className='expand-left'>
                            <Collapsible trigger={translate("prog_launguages",lan)+""}>
                                <ul className='programinglaunguages'>
                                    <li>C# - {translate("abeginner",lan)}</li>
                                    <li>Java - {translate("abeginner",lan)}</li>
                                    <li>Kotlin - {translate("beginner",lan)}</li>
                                    <li>Lua - {translate("experienced",lan)}</li>
                                    <li>Javascript - {translate("experienced",lan)}</li>
                                    <li>Typescript - {translate("beginner2",lan)}</li>
                                    <li>Python - {translate("beginner",lan)}</li>
                                    <li>PHP - {translate("beginner",lan)}</li>
                                    <li>SQL - {translate("experienced",lan)}</li>
                                </ul>
                            </Collapsible>
                        </div>
                        <div className='expand-right'>
                            <Collapsible trigger={translate("jobs",lan)+""}>
                            <ul className='programinglaunguages'>
                                    <li>Gamartis s.r.o - {translate("driver",lan)}</li>
                                    <li>AfterDrive - {translate("developer",lan)}</li>
                                </ul>
                            </Collapsible>
                        </div>
                    </div>
                </span>
            </animated.div>
            <ContactButton/>
        </div>
      );
}
export {Content};